import React, { useContext, useEffect, useState } from "react";
import { GlobalState } from "../../Context/globalState";
import classes from "./checkout.module.css";
import Stars from "../UI/Stars/stars";
import AlertDialogSlide from "../UI/paymentDetails/paymentDetails";
import axios from "axios";
import Spinner from "../UI/spinner/spinner";

const Checkout = () => {
  const { id } = useContext(GlobalState);
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get(
        "https://social-app-1a88b-default-rtdb.firebaseio.com/-MnRiJsbSKAmsRa5deHr.json"
      )
      .then((res) => {
        console.log(res.data);
        const fetchedData = res.data;
        const arr = [];
        for (let house in fetchedData) {
          arr.push(fetchedData[house]);
        }
        setData(arr);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const house = data.find((item) => item.id === id);

  function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  console.log("House is", house);

  return (
    <div className={classes.checkout}>
      {house ? (
        <div className={classes.offer}>
          <div className={classes.left}>
            <h3>{house.name}</h3>
            <span>
              <i className="fas fa-map-marker-alt"></i>
              {house.place}
            </span>
            <p>
              Every person is looking to build a life on their own needs an
              apartment to live on their own terms,If you are looking to start a
              business in the real estate industry, you need to set up your own
              company first.
            </p>
            <span>{numberWithCommas(house.price)} $</span>
            <div className={classes.rating}>
              <Stars rate={house.rate} />
            </div>
            <div className={classes.snackbar}>
              <AlertDialogSlide />
            </div>
          </div>
          <div className={classes.right}>
            <img
              src={
                house.src
                  ? house.src
                  : "https://1080motion.com/wp-content/uploads/2018/06/NoImageFound.jpg.png"
              }
              alt="House"
            />
          </div>
        </div>
      ) : (
        <Spinner />
      )}
    </div>
  );
};

export default Checkout;
