import React, { useEffect, useState } from "react";
import classes from "./view.module.css";
import HouseCard from "../../Components/UI/houseCards/houseCard";
import Slider from "../../Components/UI/swiper/swiper";
import { Route } from "react-router";
import Checkout from "../../Components/Checkout/checkout";
import axios from "axios";
import Spinner from "../../Components/UI/spinner/spinner";

const View = () => {
  const [data, setData] = useState([]);
  const [sort, setSort] = useState("date");

  useEffect(() => {
    axios
      .get(
        "https://social-app-1a88b-default-rtdb.firebaseio.com/-MnRiJsbSKAmsRa5deHr.json"
      )
      .then((res) => {
        console.log(res.data);
        const fetchedData = res.data;
        const arr = [];
        for (let house in fetchedData) {
          arr.push(fetchedData[house]);
        }
        setData(changeHandler(arr));
      })
      .catch((error) => {
        console.log(error);
      });
  }, [sort]);

  const changeHandler = (arr) => {
    if (sort === "price") {
      arr.sort((a, b) => {
        let x = a.price.toString().replaceAll(",", "");
        let y = b.price.toString().replaceAll(",", "");
        // console.log("A Price :", x, "B price", y);
        return x - y;
      });
    } else if (sort === "date") {
      arr.reverse();
    } else return arr;

    return arr;
  };

  return (
    <div className={classes.view}>
      <div className={classes.img}>
        <div className={classes.text}>
          <h2>Start Selling and Buying Houses Now.</h2>
          <p>
            " Every person is looking to build a life on their own needs an
            apartment to live on their own terms "
          </p>
        </div>
      </div>

      <div className={classes.container}>
        <div className={classes.header}>
          <div className={classes.left}>
            <i className="fas fa-chevron-left"></i>
            <i className="fas fa-chevron-right"></i>
          </div>

          <div className={classes.right}>
            <select onChange={(e) => setSort(e.target.value)} value={sort}>
              <option defaultValue="" disabled>
                Sort By
              </option>
              <option value="date">Last Modified</option>
              <option value="price">Price</option>
            </select>
          </div>
        </div>

        <div className={classes.cards}>
          {data.length > 0 ? (
            data.map((house, index) => {
              return (
                <HouseCard key={index} house={house} type="detailedCard" />
              );
            })
          ) : (
            <Spinner />
          )}
        </div>
      </div>

      <div className={classes.slider}>
        <h3>Special Offers</h3>
        <Slider />
      </div>
      <Route exact path="/view/checkout" component={Checkout} />
    </div>
  );
};

export default View;
