import React, { useState } from "react";
import classes from "./login.module.css";
import {
  createUserWithEmailAndPassword,
  onAuthStateChanged,
  signOut,
  signInWithEmailAndPassword,
} from "firebase/auth";
import { auth } from "../../firebaseConfig";
import Notistack from "../../Components/UI/Snackbar/snackbar";
import Spinner from "../../Components/UI/spinner/spinner";

const Login = () => {
  const [registerEmail, setRegisterEmail] = useState("");
  const [registerPassword, setRegisterPassword] = useState("");
  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");
  const [loginError, setLoginError] = useState("");
  const [registerError, setRegisterError] = useState("");
  const [user, setUser] = useState({});
  const [hasAccount, setHasAccount] = useState(true);
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const register = async () => {
    setLoading(true);
    try {
      const user = await createUserWithEmailAndPassword(
        auth,
        registerEmail,
        registerPassword
      );
      console.log("User: ", user);
      setOpen(true);
      setUser(user);
      setLoading(false);
      clearFileds();
    } catch (error) {
      setRegisterError(error.message);
      console.log("Register Error:", error);
      setLoading(false);
    }
  };

  const login = async () => {
    setLoading(true);
    try {
      const user = await signInWithEmailAndPassword(
        auth,
        loginEmail,
        loginPassword
      );
      console.log("User: ", user);
      setOpen(true);
      setUser(user);
      setLoading(false);
      clearFileds();
    } catch (error) {
      setLoginError(error.message);
      setLoading(false);
    }
  };

  const logout = async () => {
    await signOut(auth);
    setTimeout(() => {
      setOpen(false);
    }, 3000);
  };

  const clearFileds = () => {
    setRegisterEmail("");
    setRegisterPassword("");
    setRegisterError("");
    setLoginEmail("");
    setLoginPassword("");
    setLoginError("");
  };

  onAuthStateChanged(auth, (currentUser) => {
    setUser(currentUser);
  });

  const userEmail = user.email.find("@");

  return (
    <div className={classes.login}>
      {open ? (
        <Notistack
          msg={"You Have Successfully Signed In !"}
          type={"success "}
        />
      ) : null}

      <div className={classes.wrapper}>
        {!user ? (
          hasAccount ? (
            <>
              <h3>Log In </h3>
              <input
                type="email"
                value={loginEmail}
                onChange={(e) => setLoginEmail(e.target.value)}
                placeholder="example@gmail.com"
                className={classes.input}
                autoFocus
              />

              <input
                type="password"
                value={loginPassword}
                onChange={(e) => setLoginPassword(e.target.value)}
                placeholder="Password"
                className={classes.input}
              />
              <span className={classes.error}>{loginError}</span>

              {loading ? (
                <Spinner />
              ) : (
                <button className={classes.button} onClick={login}>
                  Sign In
                </button>
              )}
              <span className={classes.toggleMsg}>
                If you dont have an account, you can
                <span onClick={() => setHasAccount(!hasAccount)}> Sign Up</span>
              </span>
            </>
          ) : (
            <>
              <h3>Sign Up</h3>
              <input
                type="email"
                value={registerEmail}
                onChange={(e) => setRegisterEmail(e.target.value)}
                placeholder="example@gmail.com"
                className={classes.input}
                autoFocus
              />

              <input
                type="password"
                value={registerPassword}
                onChange={(e) => setRegisterPassword(e.target.value)}
                placeholder="Password"
                className={classes.input}
              />
              <span className={classes.error}>{registerError}</span>
              {loading ? (
                <Spinner />
              ) : (
                <button onClick={register} className={classes.button}>
                  Sign Up
                </button>
              )}

              <span className={classes.toggleMsg}>
                If you already have an account, you can
                <span onClick={() => setHasAccount(!hasAccount)}> Sign In</span>
              </span>
            </>
          )
        ) : (
          <>
            <div className={classes.logout}>
              <span> You Are Signed In As</span>
              <h4>{user ? user.email : ""}</h4>
              {user ? <button onClick={logout}>Sign out</button> : null}{" "}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Login;
