import axios from "axios";
import React, { useEffect, useState } from "react";
import classes from "./Dashboard.module.css";
import Notistack from "../../Components/UI/Snackbar/snackbar";
import { auth } from "../../firebaseConfig";
import { onAuthStateChanged } from "firebase/auth";
import Spinner from "../../Components/UI/spinner/spinner";

const Dashboard = () => {
  const [name, setName] = useState("");
  const [location, setLocation] = useState("");
  const [price, setPrice] = useState("");
  const [imgURL, setImgURL] = useState("");
  const [housesId, setHousesId] = useState("");
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState({});

  onAuthStateChanged(auth, (currentUser) => {
    setUser(currentUser);
  });

  useEffect(() => {
    axios
      .get(
        "https://social-app-1a88b-default-rtdb.firebaseio.com/-MnRiJsbSKAmsRa5deHr.json"
      )
      .then((res) => {
        console.log(res.data);
        const fetchedData = res.data;
        setHousesId(Object.keys(fetchedData).length);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  console.log("ID = ", housesId);

  const clickHandler = (e) => {
    e.preventDefault();
    const house = {
      id: housesId + 1,
      name: name,
      place: location,
      price: price,
      rate: Math.floor(Math.random() * (5 - 1)) + 1,
      src: imgURL,
      type: "detailedCard",
      userName: `@${user.email.slice(0, user.email.indexOf("@"))}`,
    };
    setLoading(true);

    axios
      .post(
        "https://social-app-1a88b-default-rtdb.firebaseio.com/-MnRiJsbSKAmsRa5deHr.json",
        house
      )
      .then((res) => {
        setOpen(true);
        setLoading(false);
        clearInputs();
      })
      .then((error) => {
        setLoading(false);
      });
  };

  const clearInputs = () => {
    setName("");
    setLocation("");
    setPrice("");
    setImgURL("");
    setTimeout(() => {
      setOpen(false);
    }, 3000);
  };

  return (
    <div className={classes.dashboard}>
      {open ? (
        <Notistack msg="You have successfully added a new house!" />
      ) : null}

      <div className={classes.box}>
        <h3>Add New House</h3>
        <form onSubmit={clickHandler}>
          <input
            type="text"
            placeholder="House name"
            onChange={(e) => setName(e.target.value)}
            value={name}
            maxLength={30}
            minLength={5}
            required
          />

          <input
            type="text"
            placeholder="House location"
            onChange={(e) => setLocation(e.target.value)}
            value={location}
            maxLength={30}
            required
          />

          <input
            type="number"
            placeholder="House Price"
            onChange={(e) => setPrice(e.target.value)}
            value={price}
            required
          />

          <input
            type="url"
            placeholder="Image URL"
            onChange={(e) => setImgURL(e.target.value)}
            value={imgURL}
            required
          />
          <div className={classes.btn}>
            {loading ? <Spinner /> : <button> Add New House</button>}
          </div>
        </form>
      </div>
    </div>
  );
};

export default Dashboard;
