import React, { useState, useRef } from "react";
import "./ContactUs.css";
import contactImg from "../../Photos/contact.svg";
import emailjs from "emailjs-com";
import Notistack from "../UI/Snackbar/snackbar";

const ContactUs = (props) => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const [snackbar, setSnackbar] = useState(false);

  const form = useRef();

  const clearInputs = () => {
    setEmail("");
    setName("");
    setMessage("");
    setTimeout(() => {
      setSnackbar(false);
    }, 3000);
  };

  const ClickHandler = (e) => {
    e.preventDefault();
    setLoading(true);
    emailjs
      .sendForm(
        "service_ltj2944",
        "Real_Estate_App",
        form.current,
        "user_D9y1Gws0dAtyKLW4zOemd"
      )
      .then(
        (res) => {
          console.log(res.text);
          setLoading(false);
          setSnackbar(true);
          clearInputs();
        },
        (error) => {
          console.log(error.text);
          setLoading(false);
        }
      );
  };

  return (
    <div className="wrapper ">
      {snackbar ? (
        <Notistack msg={"You Have successfully sent an E-mail!"} />
      ) : null}

      <div className="left">
        <img src={contactImg} alt="Faw" />
      </div>
      <form ref={form} className="right" onSubmit={ClickHandler}>
        <div className="input">
          <input
            type="text"
            placeholder="Name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            name="name"
            required
          />
        </div>
        <div className="input">
          <input
            type="email"
            placeholder="E-mail"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            name="email"
            required
          />
        </div>
        <div className="input">
          <textarea
            placeholder="Message"
            rows="5"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            name="message"
            required
          />
        </div>
        <button>{loading ? "Sending..." : "Send"}</button>
      </form>
    </div>
  );
};

export default ContactUs;
